import Link from 'next/link'
import React, { useState } from 'react'

const Header = () => {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const navLinks = [
        {label: 'Home', href:'#'},
        {label: 'Services', href:'#services'},
        {label: 'Portfolio', href:'#portfolio'},
        {label: 'About', href:'#about'},
        {label: 'Contact', href:'#contact'}
    ];

  return (
    <header className='sticky top-0 z-50 bg-cream border-b border-gray-200 shadow-sm'>
        <nav className='container-x py-4'>
            <div className='flex justify-between items-center'>
                {/* logo */}
                <div className='flex items-center'>
                    <Link href="/" className='text-2xl font-bold text-dark tracking-tight'>
                    Luxe<span className='text-gold'>Design</span>
                    </Link>
                    <span className='ml-2 text-xs text-green font-medium'>Manali</span>
                </div>
                {/* desktop nav */}
                <div className='hidden md:flex items-center gap-8'>
                    {navLinks.map((link)=>(
                        <Link
                        key={link.label}
                        href={link.href}
                        className='nav-link text-dark'>
                            {link.label}
                        </Link>
                    ))}
                </div>

                {/* CTA Button */}
                <div className='flex items-center gap-4'>
                    <button className='hidded sm:block btn-primary'>
                        Get Quote
                    </button>
                {/* </div> */}
                {/* Hamburger */}
                <button
                onClick={()=>setIsMenuOpen(!isMenuOpen)} className='md:hidden flex-col gap-1.5 focus:outline-none'
                aria-label='Toggle menu'>
                    <span className={`w-6 h-0.5 bg-dark translation-all duration-300 ${isMenuOpen? 'rotate-45 translate-y-2': ''}`}/><span
                    className={`w-6 h-0.5 bg-dark translation-all duration-300 ${isMenuOpen?'opacity-0':''}`}/>
                    <span
                    className={`w-6 h-0.5 bg-dark translation-all duration-300 ${isMenuOpen?'-rotate-45 -translate-y-2':''}`}/>
                </button>
            </div>
         </div>
         {/* Mobile nav */}
         {isMenuOpen && (
            <div className='md:hidden mt-4 pb-4 border-t border-gray-200'>
                <div className='flex flex-col gap-3 mt-4'>
                    {navLinks.map((link)=>(
                        <Link
                        key={link.label}
                        href={link.href}
                        className='nav-link text-dark py-2'
                        onClick={()=> setIsMenuOpen(false)}>
                            {link.label}
                        </Link>
                    ))}
                    <button className='btn-primary w-full mt-2'>Get Quote</button>
                </div>
            </div>
         )}

           
        </nav>
    </header>
  )
}

export default Header
